@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.photo-adjust {
  overflow: hidden;
  background-color: yellow;
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 400px;
  border-radius: 50%;
  display: flex;
  padding: 8px;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.photo-adjust::before {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 400px;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: rotate 10s linear infinite;
}
.photo-adjust::before {
  box-shadow: 1px 10px 4px -1px gray, 9px 0px 4px 1px rgb(218, 33, 33),
    -13px 5px 4px 1px rgb(11, 68, 149), -1px -1px 4px 8px rgb(37, 210, 75);
  transform-style: preserve-3d;
}
.photo-adjust img {
  position: absolute;
  padding: 5px;
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 400px;
  border-radius: 50%;
  width: 100%;
}
