/* mobile view */
.button {
  position: relative;
  width: 150px;
  height: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  /* border: 1px solid #217493; */
  border-radius: 4px;
  background-image: var(--button-theme);
  text-decoration: none;
  overflow: hidden;
}

.button,
.button__icon,
.button__text {
  transition: all 0.3s;
}

.button .button__text {
  transform: translateX(22px);
  color: #fff;
  font-weight: 600;
}

.button .button__icon {
  position: absolute;
  transform: translateX(109px);
  height: 100%;
  width: 39px;
  background-color: var(--primary-theme-for-app-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.button .svg {
  width: 20px;
  fill: #fff;
}

.button:hover {
  background-color: var(--primary-theme-for-app-color);
}

.button:hover .button__text {
  color: transparent;
}

.button:hover .button__icon {
  width: 148px;
  transform: translateX(0);
}

.button:active .button__icon {
  background-color: var(--primary-theme-for-app-color);
}

.button:active {
  background-color: var(--primary-theme-for-app-color);
}
.Get-in-touch {
  border-radius: 0.25rem;
  /* text-transform: uppercase; */
  font-style: normal;
  font-weight: 400;
  padding-left: 25px;
  padding-right: 25px;
  color: #fff;
  font-weight: 700;
  -webkit-clip-path: polygon(
    0 0,
    0 0,
    100% 0,
    100% 0,
    100% calc(100% - 15px),
    calc(100% - 15px) 100%,
    15px 100%,
    0 100%
  );
  clip-path: polygon(
    0 0,
    0 0,
    100% 0,
    100% 0,
    100% calc(100% - 15px),
    calc(100% - 15px) 100%,
    15px 100%,
    0 100%
  );
  height: 40px;
  font-size: 0.7rem;
  line-height: 14px;
  letter-spacing: 1.2px;
  transition: 0.2s 0.1s;
  background-image: var(--button-theme);
  border: 0 solid;
  overflow: hidden;
}

.Get-in-touch:hover {
  cursor: pointer;
  transition: all 0.3s ease-in;
  padding-right: 30px;
  padding-left: 30px;
}

.Skill-logo {
  display: flex;

  width: 100%;
  height: 100%;
  overflow: hidden;
  justify-content: center;
  align-items: center;

  /* background-color: var(--primary-theme-for-app-color); */
}
.Lang-icon {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  animation: scroll 10s linear infinite;
  padding: 10px;
  /* gap: 5vw; */
}
.Lang-icon img {
  width: 70px;
  height: 70px;
  background-color: #fff;
  padding: 5px;
  border-radius: 10px;
  box-shadow: 0px 2px 10px lightgray;
  /* aspect-ratio: 4/4; */
}
.Lang-icon img:hover {
  padding: 1px;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes scroll-right {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
